import {
  Container,
  DisplayObject,
  InteractionEvent,
  InteractionManager,
  Point,
  Sprite,
} from "pixi.js";
import { App } from "./App";
import { Component } from "./Component";
import { EventHandler } from "./events/EventHandler";
import { Logger } from "./Logger";

export class Interaction {
  private evts: EventHandler[] = [];

  constructor(
    private readonly mgr: InteractionManager,
    private readonly hitbox: Sprite,
    private readonly world: Container
  ) {
    hitbox.interactive = true;

    let pos = new Point(0, 0);
    const pointermove = () => {
      this.fireEvents(pos, ["hover"]);
      this.updateEvents({
        drag: (evt) => evt._pointermove(pos),
        hover: (evt) =>
          this.hitTest(pos, evt.sender)
            ? evt._pointermove(pos)
            : evt._pointerout(pos),
      });
    };

    const fn = () => {
      pointermove();
      window.requestAnimationFrame(fn);
    };
    window.requestAnimationFrame(fn);

    hitbox.on("pointermove", (event: InteractionEvent) => {
      pos.copyFrom(event.data.global);
      pointermove();
    });
    hitbox.on("pointerdown", (event: InteractionEvent) => {
      const pos = event.data.global;
      this.fireEvents(pos, ["drag"]);
      this.updateEvents({
        drag: (evt) => evt._pointerdown(pos),
        hover: (evt) => evt._pointerdown(pos),
      });
    });
    hitbox.on("pointerup", (event: InteractionEvent) => {
      const pos = event.data.global;
      this.updateEvents({
        drag: (evt) => evt._pointerup(pos),
        hover: (evt) => evt._pointerup(pos),
      });
    });

    window.addEventListener("wheel", (event: WheelEvent) => {
      const { clientX: x, clientY: y } = event;
      const pos = new Point(x, y);
      this.fireEvents(pos, ["wheel"]);
      this.updateEvents({
        wheel: (evt) => evt._wheel(pos, event),
      });
    });
  }

  private hitWorld(pos: Point): Component {
    const e = this.mgr.hitTest(pos, this.world);
    if (e == null) {
      return null;
    }
    const obj = (<any>e)._component;
    if (obj == null) {
      return null;
    }
    if ((<any>obj).ui2 != e) {
      return null;
    }
    return obj;
  }

  private isAttached(obj: DisplayObject) {
    while (obj != null) {
      if (obj === App.stage) {
        return true;
      }
      obj = obj.parent;
    }
    return false;
  }

  private hitTest(pos: Point, obj: Component) {
    if (!obj.visible) {
      return false;
    }
    const e: DisplayObject = (<any>obj).ui2;
    if (!this.isAttached(e)) {
      return false;
    }
    if (!e.hitArea) {
      return false;
    }
    const pt = new Point();
    e.worldTransform.applyInverse(pos, pt);
    return e.hitArea.contains(pt.x, pt.y);
  }

  private fireEvents(pos: Point, acceptEts: EventType[]) {
    const ets = new Set(acceptEts);
    const ids = new Set<string>();
    const filterEt = (evts: EventHandler[]) => {
      for (const evt of evts) {
        ids.add(evt.id);
        for (const et of evt.stops) {
          ets.delete(et);
        }
      }
    };
    filterEt(this.evts);
    Logger.log("accept event types", ets);

    const li = [];
    do {
      const obj = this.hitWorld(pos);
      if (obj == null) {
        break;
      }
      Logger.log("hit target", obj);
      const evts: EventHandler[] = [];
      ets.forEach((et) => {
        const li = obj.getHandlers(et);
        // debug.log(obj, et, li);
        for (const evt of li) {
          if (!ids.has(evt.id)) {
            evts.push(evt._bootstrap());
          }
        }
      });
      if (evts.length) {
        Logger.log("fire events", evts);
        this.evts = this.evts.concat(evts);
        filterEt(evts);
      }
      const e: DisplayObject = (<any>obj).ui2;
      e.interactive = false;
      li.push(e);
    } while (ets.size > 0);

    for (const e of li) {
      e.interactive = true;
    }
  }

  private updateEvents(evts: {
    [K in EventType]?: (e: EventHandler) => EventHandler;
  }) {
    this.evts = this.evts.flatMap((evt) => {
      const fn = evts[evt.type];
      if (fn == null) {
        return [evt];
      } else {
        const evt1 = fn(evt);
        if (evt1 == null) {
          Logger.log("cancel event", evt);
          return [];
        }
        if (evt1 == evt) {
          Logger.log("update event", evt);
        } else {
          Logger.log("fire new event", evt, "->", evt1);
        }
        return [evt1];
      }
    });
  }
}
