// TODO: move bootstrap logic to App
import "./2d/pixi/App";

import { WebfontLoaderPlugin } from "pixi-webfont-loader";
import { Loader, WRAP_MODES } from "pixi.js";

async function bootstrap() {
  const response = await fetch("assets.json");
  const manifest = await response.json();

  Loader.registerPlugin(WebfontLoaderPlugin);
  const loader = Loader.shared;
  loader.add({ name: "Splatoon", url: "assets/fonts/Splatoon1.otf" });
  for (const [name, url] of Object.entries(manifest)) {
    if (!name.startsWith("fonts/")) {
      loader.add({
        name,
        url: <string>url,
      });
    }
  }
  loader.onComplete.once(async () => {
    for (const name of Object.keys(manifest)) {
      if (!name.startsWith("fonts/")) {
        loader.resources[name].texture.baseTexture.wrapMode = WRAP_MODES.REPEAT;
      }
    }
    const { main } = await import("./Main");
    main();
  });
  loader.load();
}

bootstrap();
